.event {
    padding: 10px 10px 35px;
    margin-bottom: 10px;
    text-align: left;
    border: 1px solid #ccc;
    border-radius: 4px;
    position: relative;
    background-color: white;
}
.col-md-6, .col-lg-4 {
    align-items: center;
}
ul {
  padding: 0;
  list-style: none;
}
  
  input[type="text"],
  input[type="number"] {
    padding: 8px 6px 8px 10px;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
    font-size: 14px;
    line-height: 20px;
    align-self: center;
    height: 38px;
  }
  
  .city {
    width: 250px;
  }
  
  .CitySearch {
    display: flex;
    flex-direction: column;
    flex-basis: 300px;
    max-width: 300px;
    margin: auto;
    position: relative;
    padding-bottom: 30px;
  }
  
  .suggestions {
    width: 250px;
    /* position: absolute; */
    z-index: 2;
    align-self: center;
    margin: 0;
    top: 43px;
    font-size: 14px;
    box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
  }
  
  .suggestions > li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #eee;
    cursor: default;
  }
  
  .suggestions > li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  
  .suggestions > li:hover {
    background-color: #bcdcdb;
  }
.total-events-viewed {
    width: 200px;
    margin: 20px auto;
    text-align: center;
  }
